<template>

    <component :is="this.$route.meta.layout || 'div'">
      <nointernet/>
      <v-banner
        v-if="deferredPrompt"
        color="info"
        dark
        class="text-left rounded-lg mb-6"
      >
        {{$t('install_app')}}
        
        <template v-slot:actions>
          <v-btn text @click="dismiss">{{$t('dismiss')}}</v-btn>
          <v-btn text @click="install">{{$t('install')}}</v-btn>
        </template>
      </v-banner>

      <router-view />

      <v-snackbar bottom right :value="updateExists" :timeout="-1" color="primary">
          {{$t('new_contet')}}
        <v-btn text @click="refreshApp">
          {{$t('update')}}
        </v-btn>
      </v-snackbar>
    </component>
    

</template>
<style >
    .page{
        height: 100%;
        width: 100%;
        padding-bottom: 0%;
    }
</style>

<script>
import update from './mixins/update'
import Cookies from "js-cookie";
import nointernet from "./components/Helpers/Errors/NoInternet.vue"
export default {    
  components: {
      nointernet
  },
  name: 'App',

  data: () => ({
    deferredPrompt: null,
    onLine: null,
  }),
  mixins: [update],
  
  created() {
    window.addEventListener("beforeinstallprompt", e => {
      e.preventDefault();
      // Stash the event so it can be triggered later.
      if (Cookies.get("add-to-home-screen") === undefined) {
        this.deferredPrompt = e;
      }
    });
    window.addEventListener("appinstalled", () => {
      this.deferredPrompt = null;
    });
  },
  methods: {
    async dismiss() {
      Cookies.set("add-to-home-screen", null, { expires: 7 });
      this.deferredPrompt = null;
    },
    async install() {
      this.deferredPrompt.prompt();
    },

  }
};
</script>
