import Vue from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import './plugins/ant-design-vue.js';
import vuetify from './plugins/vuetify'
import 'es6-promise/auto';

import axios from 'axios';
import VueAxios from 'vue-axios';
Vue.use(VueAxios, axios)

Vue.prototype.$baseUrl = process.env.VUE_APP_API_RESOURCE_URL
axios.defaults.baseURL = process.env.VUE_APP_API_URL


import store from './store'
import router from './router'
import { abilitiesPlugin } from '@casl/vue';
import {ability} from './Helpers/ability.js'
import {i18n} from './i18n.js'
import interceptorsSetup from './Helpers/interceptors'
import NProgress from 'vue-nprogress'
//VueGoogleMaps
import * as VueGoogleMaps from "vue2-google-maps";
Vue.use(VueGoogleMaps, {
  load: {
    key: "AIzaSyDYU9QZbgp6ctkbFIpHR0km9Lu7kh0Z2Ac",
  },
  installComponents: true,
});

interceptorsSetup()
Vue.use(abilitiesPlugin, ability);
const optionsNProgress = {
  latencyThreshold: 100, // Number of ms before progressbar starts showing, default: 100,
  router: true, // Show progressbar when navigating routes, default: true
  http: true // Show progressbar when doing Vue.http, default: true
};

Vue.use(NProgress, optionsNProgress)

const nprogress = new NProgress()

router.beforeEach((to, from, next) => {

const loggedIn = localStorage.getItem('user')
const canNavigate = to.matched.some(route => {
      if(route.meta.resource){
          return ability.can(route.meta.resource)
      }
    return true
})


if (to.matched.some(record => record.meta.auth) && !loggedIn) {
    next('/login')
    return
  }else{

    if (!canNavigate) {
        return next('/403')
    }
  }

  next()
});



Vue.config.productionTip = false

new Vue({
  router,
  store,
  vuetify,
  i18n,
  nprogress,
  created () {
    const userInfo = localStorage.getItem('user')
    if (userInfo) {
      const userData = JSON.parse(userInfo)
      this.$store.commit('setUserData', userData)
    }
  },
  render: h => h(App)
}).$mount('#app')
