<template>
  <v-app id="inspire">
   <nprogress-container></nprogress-container>
    <v-app-bar
      app
      clipped-right
      color="#FFFFFF"
      flat
      elevation="1"
      
    >
      <router-link to="/"><div class="orange--text h1" ><b>EatInMore</b></div></router-link>

         
      <v-tabs
        v-if="!$vuetify.breakpoint.smAndDown"
        centered
        class="ml-n9"
        icons-and-text
      >
        <v-tab
          v-for="(linkName, index) in linksNames"
          :key="linkName"
          :to="links[index]"
          link
          dense
          fixed-tabs
          centered
          slider-color="yellow"
          color="orange"
        >
          {{ $t(linkName) }}
          <v-icon small>{{icons[index]}}</v-icon>
        </v-tab>
      </v-tabs>
      <v-spacer></v-spacer>
        <div 
          class=""
        >
         
        </div>
        <div >
            <v-btn
            text
            small
            to="/home"
            >
            <v-icon>{{icon_home}}</v-icon>
            </v-btn>
        </div>
     
    </v-app-bar>

    <v-main>
      <v-container
         class="py-8 px-6 mb-8"
        fluid
      >
        <v-row>
          <v-col
            cols="12"
          >
          <slot/>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
      <v-btn
          v-scroll="onScroll"
          v-show="fab"
          fab
          dark
          fixed
          bottom
          right
          color="primary"
          @click="toTop"
          class="mb-11"
          small
        >
          <v-icon >mdi-chevron-up</v-icon>
      </v-btn>
    
          <v-bottom-navigation
            v-if="$vuetify.breakpoint.smAndDown"
            hide-on-scroll
            app
          >
        
          <v-tabs
            centered
            class="ml-n9"
            icons-and-text
          >
            
            <v-tab
              to="/"
              link
              dense
              centered
              slider-color="yellow"
              color="orange"
            >
              <span class="text-caption" >{{ $t('welcome') }}</span>
              <v-icon small>mdi-noodles</v-icon>
            </v-tab>
            
            <v-tab
              v-for="(linkName, index) in linksNames"
              :key="linkName"
              :to="links[index]"
              link
              dense
              fixed-tabs
              centered
              slider-color="yellow"
              color="orange"
            >
              
               <span class="text-caption" >{{ $t(linkName) }}</span>
              <v-icon small>{{icons[index]}}</v-icon>
            </v-tab>
          </v-tabs>

          </v-bottom-navigation>
          
        </v-app>
      </template>

<script>
  import NprogressContainer from 'vue-nprogress/src/NprogressContainer'
  
  export default {
    components: { NprogressContainer },
    name: 'locale-changer',

    methods: {
        onScroll (e) {
        if (typeof window === 'undefined') return
        const top = window.pageYOffset ||   e.target.scrollTop || 0
        this.fab = top > 20
        },
        toTop () {
        this.$vuetify.goTo(0)
        },
    },

    mounted() {
        const userData = JSON.parse(this.userInfo);
        if(userData){
            this.icon_home ='mdi-home-variant-outline'
        }
    },

    data: () => ({
      fab: false,
      userInfo: localStorage.getItem('user'),
      icon_home: "mdi-login",
      states:[],
      drawer: true,
      iconsFoot: [
        'mdi-facebook',
        'mdi-twitter',
        'mdi-linkedin',
        'mdi-instagram',
      ],
      linksNames: [
        'our_chefs',
        'contact',
        'about_us',
        'terms_and_conditions'
      ],
      links:[
        'pub-our-chefs',
        'contact',
        'about',
        'terms'
      ],
      icons: [
        'mdi-view-dashboard',
        'mdi-phone',
        'mdi-information',
        'mdi-form-select',
      ],
    }),
  }
</script>



