import axios from 'axios';
import store from '../store';
import router from '../router'
import EventBus  from "./event-bus";

export default function setup() {
    axios.interceptors.request.use(function(config) {
        const token = store.getters.token;
        if(token) {
            config.headers.Authorization = `Bearer ${token}`;
        }
        return config;
    }, function(err) {
        store.dispatch('logout')
        return Promise.reject(err);
    });

    axios.interceptors.response.use(
    function(response) {
        if(response.status &&  response.data){
            const status = response.status;
            const data = response.data;
            statusCode(status,data);
        }
        return response;
    },
    function(error) {
        if(error.response && error.response){
            const status = error.response.status;
            const data = error.response.data;
            statusCode(status,data);
        }
        return Promise.reject(error);
    }
    );


    function event(text, color) {
        EventBus.$emit('snackBar', {
            text: text,
            color: color,
            model: true,
        })
    }
    function statusCode(status,data) {
        if (status === 200) {
            if(data.success)
                {
                    var color
                    if(data.color){
                        color = data.color
                    }else{
                        color = 'success'
                    }
                    event(data.success, color);
                }
        }else if (status === 401) {
            event("Please login.", 'orange darken-1');
            router.push('login').catch(()=>{});
            //store.dispatch('logout');
        }else if(status === 403){
            event(data.message, 'red lighten-1');
        }else if(status === 422){
            event(data.errors[0], 'red lighten-1');
        }else if(status === 429){
            event("Try again later.", 'red lighten-1');
        }else{
            event("Connection error, please reload the page.", 'red lighten-1');
        }


    }
}


