<template>
    <div>
        <vue-internet-checker
            @status="status"
        />
    
        <v-banner
            v-if="onLine == false"
            single-line
            color="warning"
            dark
            class="text-left rounded-lg mb-6"
        >
            <v-icon
            slot="icon"
            color="white"
            >
            mdi-wifi-strength-alert-outline
            </v-icon>
            {{$t('no_internet')}}
        </v-banner>
    </div>      
    
</template>
<script>
import vueInternetChecker from 'vue-internet-checker';
export default {    
  components: {
      vueInternetChecker,
  },

  data: () => ({
    onLine: null,
  }),

  methods: {
    status(ele) {
      //console.log(ele);
      this.onLine = ele;
    },

  }
};
</script>